.footerMain {
  width: 100%; }

.footerInner {
  composes: PVS;
  margin-left: auto;
  margin-right: auto;
  padding-left: 2rem;
  padding-right: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-top: 1px solid #E8E7E7; }
  @media screen and (min-width: 768px) {
    .footerInner {
      max-width: 800px; } }
  @media screen and (min-width: 1024px) {
    .footerInner {
      max-width: 1200px; } }
  @media (min-width: 1440px) {
    .footerInner {
      max-width: 1400px;
      padding-left: 3rem;
      padding-right: 3rem; } }
  @media screen and (min-width: 768px) {
    .footerInner {
      padding-top: 16px;
      padding-bottom: 16px;
      flex-direction: row;
      margin-left: 4rem;
      margin-right: 4rem; }
      .footerInner .column {
        flex: 1; }
        .footerInner .column:last-child {
          text-align: right; } }
  @media screen and (min-width: 1024px) {
    .footerInner {
      margin: 0 auto;
      margin-left: 4rem;
      margin-right: 4rem; } }

.footerLink {
  display: inline-block;
  padding: 10px;
  font-size: 0.75rem;
  color: #A8A4A4 !important;
  text-decoration: none; }
  .footerLink:hover {
    color: #4A4A4A; }

.bodyBorder a {
